import bandService from '.././services/bands'

const bandReducer = (state = [], action) => {
  console.log('ACTION: ', action)
  switch (action.type) {
  case 'INIT_BANDS':
    return action.data
  case 'ADD_ABOUT':
    return [ ...state.filter(b => b.id !==action.data.id), action.data ]
  case 'CREATE':
    return [ ...state, action.content]
  default:
    return state
  }

}

export const creation = (content) => {
  return async (dispatch) => {
    const newBand = await bandService.createNew(content)
    dispatch({
      type: 'CREATE',
      content: newBand
    })
  }
}

export const addAbout = (band, about) => {
  return async (dispatch) => {
    const newObject = { ...band, about: about }
    const updatedBand = await bandService.update(band._id, newObject)
    dispatch({
      type: 'ADD_ABOUT',
      data: updatedBand
    })
  }
}

export const initialization = () => {
  return async (dispatch) => {
    const bands = await bandService.getAll()
    dispatch({
      type: 'INIT_BANDS',
      data: bands
    })
  }
}

export default bandReducer


